.sf-editor-toolbar {
  height: 39px;
  background-color: #fcfcfc;
}

.sf-editor-toolbar .iconfont {
  font-size: 13px;
}

.sf-editor-toolbar .icon-drop-down {
  display: inline-block;
  transform: scale(0.8);
}

.sf-editor-toolbar .icon-caret-up {
  display: inline-block;
  transform: scale(0.8);
}

/* reset menu style*/
.sf-editor-toolbar .menu > * {
  display: inline-block;
}

.sf-editor-toolbar .menu > * + * {
  margin-left: 10px;
}

.sf-editor-toolbar .menu {
  margin-right: 20px;
}

.seafile-btn-group div button {
  border: none;
}

.seafile-btn-group  div button:not(.dropdown-item) {
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  border-right: 1px solid #e5e5e5;
  border-radius: 0;
}

.seafile-btn-group div:first-child button {
  border-radius: 3px 0 0 3px;
  border-left: 1px solid #e5e5e5;
}

.seafile-btn-group div:last-child button {
  border-radius: 0 3px 3px 0;
  border-right: 1px solid #e5e5e5;
}

.seafile-btn-group div button:disabled {
  border-color: #e5e5e5;
}

.btn-active {
  color: #0c0c0c;
  cursor: pointer;
}

.rich-icon-btn[data-active='true'] {
  color: #eb8205;
}

.rich-icon-btn.svg-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-active[data-active='true'] {
  color: #eb8205;
}

.tableToolBar {
  font-size: 12px;
  display: inline-flex;
}

.tableToolBar:after {
  content: '';
  display: block;
  clear: both;
}

.tableToolBar div .symbol {
  font-size: 14px;
}

.tableToolBar .btn {
  color: #555555;
}

.tableToolBar div:not(first):not(.drop-list) {
  /*display: inline-block;*/
  margin-left:10px;
}

.tableToolBar .table-toolbar-btn {
  line-height: 28px;
  padding: 0 15px;
  height: 30px;
}

.table-toolbar-group {
  height: 100%;
  display: flex;
  align-items: center;
  border-right: 1px solid #e5e5e5;
  padding-right: 10px;
}

.sf-editor-table-btn-wrapper {
  position: relative;
}

.sf-editor-table-count-card {
  background-color: #ffffff;
  min-width: 100px;
  min-height: 100px;
  position: absolute;
  top: 101%;
  left: 0;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 5%);
  border: 1px solid rgba(0, 40, 100, 0.12);
  z-index: 100;
  padding: 5px 10px 10px 10px;
  border-radius: 2px;
}

.sf-editor-table-cells-header {
  text-align: center;
  height: 20px;
  font-size: 14px;
}

.sf-editor-table-card {
  margin-top: 5px;
}

.sf-editor-table-card-row {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #cccccc;
}

.sf-editor-table-card-row:first-child {
  border-top: 1px solid #cccccc;
}

.sf-editor-table-card-cell {
  border-right: 1px solid #cccccc;
  width: 20px;
  height: 15px;
}

.sf-editor-table-card-row .sf-editor-table-card-cell:first-child {
  border-left: 1px solid #cccccc;
}

.sf-editor-table-card-active-cell {
  background-color: #ffa94d;
}

.rich-menu {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 39px;
  align-items: center;
  padding: 0 10px;
  background-color: #fcfcfc;
}

.topbar-head {
  width: 100%;
  height: 56px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  padding: 4px 10px;
}

.button-container {
  height: 100%;
  display: flex;
  align-items: center;
}

.button-container .btn {
  background-color: #fcfcfc;
}

.button-container .rich-icon-btn {
  background-color: #fcfcfc;
}

.button-container .dropdown-menu-right .iconfont {
  display: inline-block;
  transform: scale(0.8);
  font-size: 0.875rem;
  font-weight: 900;
}

.header-list-container {
  height: 39px;
  border-right: 1px solid #e5e5e5;
  width: 140px;
  padding: 5px 5px;
  box-sizing: border-box;
  border-left: 1px solid #e5e5e5;
}

.header-list-container .header-list {
  position: relative;
  width: 100%;
  height: 100%;
}

.header-list-container .header-list-body-highlight {
  background-color: #e5e5e5;
}

.header-list-container .header-list-body-hover:hover {
  background-color: #e5e5e5;
}

.header-list-container .list-body {
  width: 100%;
  height: 100%;
  display: flex;
  line-height: 100%;
  vertical-align: middle;
  justify-content: space-between;
  border-radius: 2px;
  padding: 0 3px;
}

.list-body div {
  display: inline-block;
  line-height: 30px;
}

.header-list-container .list-dropdown {
  position: absolute;
  left: 0;
  top: 32px;
  border: 1px solid #e5e5e5;
  border-radius: 2px;
  padding: 8px 0;
  box-shadow: 0 0 10px #ccc;
  background-color: #ffffff;
  min-width: max-content;
  box-sizing: border-box;
}

.header-list-container .header-list .list-dropdown-item {
  cursor: pointer;
  height: 30px;
  width: 100%;
  padding: 4px 24px;
}

.header-list-container .header-list .list-dropdown-item:hover {
  background-color: rgb(245, 245, 245);
}

.rich-icon-btn {
  border: 1px solid transparent;
  height: 100%;
  width: 30px;
  line-height: 30px;
  margin-right: 10px;
  color: #555555;
}

.rich-icon-btn-disabled {
  color: #999999;
}

.rich-icon-btn-hover:hover {
  background-color: #e5e5e5;
  border-radius: 2px;
}

.rich-dropdown {
  border: none;
  margin-left: 10px;
  color: #555555;
}

p.tip {
  color: #bbb;
}

.tag a {
  color: #fff; 
  text-decoration: underline !important;
}

.tag a:hover {
  color: #eb8205 !important;
}

.custom-dropdown-list {
  position: relative;
  margin-right: 10px;
}

.custom-dropdown-list .dropdown-list-toggle {
  width: 30px;
  height: 28px;
  text-align: center;
  line-height: 28px;
  font-size: 0.75rem;
}

.custom-dropdown-list .dropdown-list-toggle:hover {
  background-color: #e5e5e5;
  border-radius: 3px;
}

.custom-dropdown-list .custom-dropdown-menu {
  position: absolute;
  left: 0;
  font-size: 0.85rem;
  top: 28px;
  display: block;
  background-color: #ffffff;
}

@media (max-width: 991.8px) {
  .seafile-md-viewer-main-panel {
    margin: 0 2%;
  }
  .rich-menu {
    overflow-x: auto;
  }
  .rich-menu::-webkit-scrollbar{
    display: none;
  }
  .rich-menu .button-container {
    width: stretch;
  }
  .list-body div {
    margin-left: 10px;
  }
}
